.key.white {
  cursor: pointer;
  z-index: 1;
  background-color: #fff;
  border-radius: 0 0 .3vw .3vw;
  margin: 1px;
  transition: all .3s;
}

.key.black {
  cursor: pointer;
  z-index: 2;
  background-color: #000;
  border-radius: 0 0 .3vw .3vw;
  transition: all .3s;
  box-shadow: -10px 10px 10px #00000021;
}

.key:hover {
  filter: invert(.1);
  transition: all .1s;
}

.black.key.selectedKey {
  background-color: #906e11;
  transition: all .2s;
  transform: scaleY(.99)translateY(-.5%);
}

.white.key.selectedKey {
  background-color: #fb0;
  transition: all .2s;
  transform: scaleY(.99)translateY(-.5%);
}

.key.selectedKey.rootNote, .key.rootNote {
  background-color: red;
  transition: background-color .3s, transform .3s;
  transform: scaleY(1.01)translateY(.5%);
  box-shadow: 0 0 10px #f00c;
}
